.main {
    margin-top: 10vh;
    display: flex;
    justify-content: center;
    gap: 60px;

    &__section--images {
        display: grid;
        grid-template-columns: repeat(4, min-content);
        grid-template-rows: repeat(2, auto);
        column-gap: 30px;
        row-gap: 30px;
        justify-content: start;
    }

    &__image {
        border-radius: 15px;
        cursor: pointer;
        line-height: 0;

        &--big {
            border-radius: 25px;
            grid-column: 1 / 5;
            grid-row: 1 / 2;
        }

        &--thumb {
            &:hover {
                border: 1px solid var(--primary-orange);
            }

            grid-row: 2 / 3;
            border: 1px solid transparent;
        }
    }
}